@import "./variables";

@mixin text($fontFamily, $fontSize, $lineHeight, $color) {
    font-family: $fontFamily;
    font-size: $fontSize;
    line-height: $lineHeight;
    color: $color;
}

@mixin container() {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
